*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
}

@-ms-viewport {
  width: device-width;
}

article, aside, dialog, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: left;
  background-color: #fff;
}

[tabindex="-1"]:focus {
  outline: none !important;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0.5rem;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
  padding: 0;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: 700;
}

dd {
  margin-bottom: .5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

dfn {
  font-style: italic;
}

b,
strong {
  font-weight: bold;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

a {
  color: #007bff;
  text-decoration: none;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

a:hover {
  color: #0056b3;
  text-decoration: underline;
}

a:not([href]):not([tabindex]) {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):focus, a:not([href]):not([tabindex]):hover {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):focus {
  outline: 0;
}

pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
  -ms-overflow-style: scrollbar;
}

figure {
  margin: 0 0 1rem;
}

img {
  vertical-align: middle;
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

a,
area,
button,
[role="button"],
input:not([type="range"]),
label,
select,
summary,
textarea {
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}

table {
  border-collapse: collapse;
}

caption {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  color: #868e96;
  text-align: left;
  caption-side: bottom;
}

th {
  text-align: inherit;
}

label {
  display: inline-block;
  margin-bottom: .5rem;
}

button {
  border-radius: 0;
}

button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="radio"],
input[type="checkbox"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: .5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: none;
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

@font-face {
  font-family: "Bernina";
  src: url("../fonts/BerninaSans-Web-CondensedBold.woff2") format("woff2");
}

.inner {
  width: 100%;
}

body {
  font-family: Bernina;
  background: #f7ffff;
}

header .background {
  width: 100%;
  background-image: url("../img/background.jpg");
  background-position: center;
  background-size: cover;
  height: auto;
  min-height: 1000px;
  background-position: top;
}

header .background .promo {
  position: absolute;
  background: #810426;
  color: white;
  border-radius: 7px;
  text-align: center;
  width: 70%;
  transform: rotate(23deg);
  margin-left: 24%;
  top: 5%;
  width: 11em;
  right: 5%;
  font-size: 2em;
  margin-top: 1em;
}

header .background .promo #text_promo_white_big {
  font-weight: bold;
  font-size: larger;
}

header .background .promo #text_promo_white_small {
  font-size: small;
}

header .background .bandeau {
  background-color: rgba(255, 255, 255, 0.25);
  color: #f7ffff;
  width: 100%;
  position: absolute;
  margin-top: 880px;
  font-size: 2em;
}

header .background .bandeau #text_white_big, header .background .bandeau #text_white_small {
  margin-left: 3%;
  font-size: 50%;
}

header .background .bandeau #text_white_big {
  font-weight: bold;
}

#titre {
  font-size: 2em;
  margin: 20px;
  color: #00465b;
}

.text {
  font-weight: bold;
  text-align: center;
  color: #00465b;
}

.text #paragraphe {
  margin-left: 12px;
  margin-right: 12px;
}

.head {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 1em;
}

.head #rdv {
  border: 1px #00465b solid;
  background-color: #f7ffff;
  border-radius: 10px;
  color: #00465b;
  height: fit-content;
  font-size: 35px;
}

#marque {
  text-align: unset;
  font-size: 76%;
  margin-left: 1%;
}

.menu {
  display: flex;
  justify-content: center;
}

.menu ul {
  list-style-type: none;
}

.menu p {
  display: flex;
  justify-content: center;
  color: #00465b;
}

.menu img {
  border-radius: 50%;
}

@media screen and (min-width: 466px) {
  .inner {
    max-width: 800px;
    margin: auto;
  }
  ul {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
  }
  ul > li {
    width: 50%;
    padding: 3%;
  }
  ul > li img {
    width: 100%;
  }
}

@media screen and (min-width: 668px) {
  .inner {
    max-width: 800px;
    margin: auto;
  }
  ul {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
  }
  ul > li {
    width: 30%;
    padding: 3%;
  }
  ul > li img {
    width: 100%;
  }
}
